@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-size: 1vw;
  width: 100%;
  overflow-x: hidden;
  background: #2A2A2A;
  color: #FFFFFF;
}

.container {
  width: 60.625vw;
  margin-left: 26.35417vw;
  padding-top: 6.09375vw;
  position: relative;
  z-index: 20;
  padding-bottom: 4.58333vw;
}

.wrapper {
  overflow: hidden;
  width: 100%;
  position: relative;
}

.wrapper::before {
  content: "";
  width: 100%;
  height: 100%;
  background: url("../img/bg_lines.svg") left -17.29167vw top repeat-y;
  background-size: 138.48958vw auto;
  display: block;
  position: absolute;
  left: 0;
  top: 30.20833vw;
  z-index: -1;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.header__menu {
  list-style: none;
  padding: 0.26042vw 0 0;
  margin: 0 0 0 20vw;
  display: flex;
  align-items: center;
}

.header__menu li + li {
  margin-left: 1.40625vw;
}

.header__menu a {
  font-weight: 300;
  font-size: 1.66667vw;
  color: #fff;
  position: relative;
  display: flex;
  align-items: center;
}

.header__menu a::before {
  content: "";
  display: block;
  width: 0.88542vw;
  height: 3.28125vw;
  background: #872C1C;
  box-sizing: border-box;
  margin-right: 0.26042vw;
  transition: 0.3s ease;
}

.header__menu a.active::before {
  background: #ed4f34;
  border: 1px solid #fff;
}

.header__menu a:hover::before {
  background: #ed4f34;
}

.static {
  position: fixed;
  display: block;
}

