.movie-block {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.movie-block + .movie-block {
  margin-top: 7.5vw;
}

.movie-block__left {
  width: 10.72917vw;
  padding-top: 4.84375vw;
}

.movie-block__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 300;
  font-size: 1.30208vw;
  text-align: left;
  color: #fff;
  width: 100%;
  height: 4.42708vw;
  background: rgba(237, 79, 52, 0.7);
  border: 4px solid rgba(255, 255, 255, 0.53);
  box-sizing: border-box;
  transition: 0.3s ease;
}

.movie-block__btn:hover {
  background: #ed4f34;
}

.movie-block__right {
  width: 47.96875vw;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.movie-block__date {
  height: 4.84375vw;
  width: 100%;
  background: #4d4d4d;
  border: 1px solid #707070;
  opacity: 0.74;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 1.40625vw;
  font-weight: 300;
  font-size: 1.97917vw;
  text-align: right;
  color: #fff;
}

.movie-block__img {
  height: 26.77083vw;
  width: 100%;
  position: relative;
}

.movie-block__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: saturate(0);
}

.movie-block__img::before {
  content: "";
  display: block;
  position: absolute;
  width: 1.09375vw;
  height: 100%;
  background: #b24330;
  left: 0;
  top: 0;
}

.movie-block__img::after {
  content: "";
  display: block;
  position: absolute;
  background: url("../img/video_after.svg") center center no-repeat;
  right: 1.40625vw;
  top: -0.72917vw;
  width: 2.08333vw;
  height: 25.15625vw;
}

.movie-block__title {
  font-weight: 300;
  font-size: 3.38542vw;
  text-align: left;
  color: #fff;
  background: rgba(237, 79, 52, 0.71);
  padding: 0 0.67708vw;
  margin-top: 0.41667vw;
}

.movie-block__info {
  margin-top: 0.26042vw;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}

.movie-block__staff {
  width: 11.82292vw;
  padding: 0.78125vw 0.625vw 5.20833vw 0.78125vw;
  box-sizing: border-box;
  flex-shrink: 0;
  position: relative;
}

.movie-block__staff::before {
  content: "";
  width: 100%;
  height: 100%;
  background: url("../img/video_staff.svg") center top no-repeat;
  background-size: 100% 100%;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}

.movie-block__staff h3 {
  font-weight: 300;
  font-size: 0.9375vw;
  text-align: left;
  color: #000;
  margin: 0;
}

.movie-block__staff ul {
  list-style: none;
  font-weight: normal;
  font-size: 0.9375vw;
  text-align: left;
  color: #000;
  line-height: 1.25vw;
}

.movie-block__staff ul + h3 {
  margin-top: 1.25vw;
}

.movie-block__text {
  width: 34.375vw;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding-top: 0.67708vw;
}

.movie-block__text p {
  font-weight: 300;
  font-size: 1.09375vw;
  text-align: left;
  color: #fff5d9;
  line-height: 1.45833vw;
}

.movie-block__sponsor {
  width: 12.8125vw;
  min-height: 7.03125vw;
  background: url("../img/video_sponsor.svg") center top no-repeat;
  background-size: 100% auto;
  box-sizing: border-box;
  padding: 0.72917vw 0.67708vw 0;
  margin-top: 2.76042vw;
}

.movie-block__sponsor h3 {
  font-weight: 300;
  font-size: 1.40625vw;
  text-align: left;
  color: #fff;
  padding-left: 0.26042vw;
}

.movie-block__sponsor div {
  font-weight: 300;
  font-size: 2.34375vw;
  text-align: left;
  color: #fff;
}

.movie-block:nth-child(even) {
  flex-direction: row-reverse;
}

.movie-block:nth-child(even) .movie-block__right {
  align-items: flex-end;
}

.movie-block:nth-child(even) .movie-block__date {
  justify-content: flex-start;
  text-align: left;
}

.movie-block:nth-child(even) .movie-block__img::before {
  left: auto;
  right: 0;
}

.movie-block:nth-child(even) .movie-block__img::after {
  right: auto;
  left: 1.40625vw;
}

.movie-block:nth-child(even) .movie-block__title {
  text-align: right;
}

.movie-block:nth-child(even) .movie-block__info {
  flex-direction: row-reverse;
}

.movie-block:nth-child(even) .movie-block__staff::before {
  transform: scaleX(-1);
}

.movie-block:nth-child(even) .movie-block__text {
  align-items: flex-start;
}

